	.sick-animation {
		margin-top: 20px;
		height: 20px;
		display: inline-block;
		span {
			display: inline-block;
			height: 100%;
			background: #FFFFFF;
			width: 0;
			transition: width  0.3s linear; } }
	.sick-animation:nth-of-type(1) {
		width: 209px;
		&>span {
			transition-delay: 0.2s; } }
	.sick-animation:nth-of-type(2) {
		width: 181px;
		&>span {
			transition-delay: 0.5s; } }
	.sick-animation:nth-of-type(3) {
		width: 151px;
		&>span {
			transition-delay: 0.8s; } }
	.sick-animation:nth-of-type(4) {
		width: 119px;
		&>span {
			transition-delay: 1.1s; } }
	.sick-animation:nth-of-type(5) {
		width: 81px;
		&>span {
			transition-delay: 1.4s; } }
	.sick-animation.animate-scale>span {
		width: 100%; }
	.sick-title {
		font-weight: 500;
		font-size: 16px;
		line-height: 25px; } }
